.w-feature{display:flex;flex-direction:column;justify-content:center;background:#f2f8fb;min-height:100vh;}.w-feature__services{width:auto;max-width:1200px;float:none;display:block;margin-right:auto;margin-left:auto;padding-left:10px;padding-right:10px;margin-top:40px;width:1200px;}.w-feature__services::after{content:'';display:table;clear:both}.w-feature__services__item{float:left;clear:none;text-align:inherit;width:31.33333333333333%;margin-left:0%;margin-right:3%;text-align:center;}.w-feature__services__item::after{content:'';display:table;clear:both}.w-feature__services__item:last-child{margin-right:0%}.w-feature__services__item__icon{font-size:42px;color:#00d5b5}.w-feature__services__item__head{font-size:18px;text-transform:uppercase;letter-spacing:.1rem;font-weight:600}.w-feature__services__item__description{letter-spacing:.1rem;font-weight:300;line-height:1.7rem;font-size:14px}.w-head{text-align:center;text-transform:uppercase;font-weight:normal;letter-spacing:.15rem;line-height:4rem;}.w-head > b{position:relative;}.w-head > b > span{position:relative;}.w-head > b > span:after{content:'';position:absolute;left:.15rem;right:.15rem;bottom:-10px;background:#00d5b5;height:5px}.w-info{padding:40px 0;letter-spacing:.1rem;text-align:center;font-weight:300;line-height:1.7rem}@media only screen and (max-width:768px){.header-nav__brand-name{display:block}.header-nav{padding:0 15px}.header-nav__logo{z-index:1}.header-nav__bar{display:none}.header-nav__bar--active{flex-direction:column;align-items:center;position:absolute;top:0;left:0;right:0;background:#fff;padding:0 80px}.header-nav__bar__link:first-child{margin-left:0}}